@import '../../scss/variables';

.todo-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4px auto;
    color: #fff;
    background: linear-gradient(
      90deg,
      $colour_9,
      $colour_10
    );
    animation-duration: 150ms;
    animation-name: jiggle;
    animation-iteration-count: 4;
  
    padding: 16px;
    border-radius: 5px;
    width: 90%;
    transform: scale(1);
    opacity: 1;
    transition: transform ease-in-out 350ms, opacity ease-in-out 350ms;

    &.remove {
      transform: scale(0);
      opacity: 0;
    }

    &.complete {
        text-decoration: line-through;
        opacity: 0.4;
    }

    &:nth-child(4n + 1) {
        background: linear-gradient(
          90deg,
          $colour_4,
          $colour_5
        );
    }

    &:nth-child(4n + 2) {
        background: linear-gradient(
          90deg,
          $colour_11,
          $colour_12
        );
    }

    &:nth-child(4n + 3) {
        background: linear-gradient(
          90deg,
          $colour_7,
          $colour_8
        );
    }

    .todo-text {
        cursor: pointer;
    }

    .icons {
        display: flex;
        align-items: center;
        font-size: 24px;
        cursor: pointer;

        .delete-icon {
            margin-right: 5px;
            color: #fff;
        }
          
        .edit-icon {
        color: #fff;
        }
    }
}

@keyframes jiggle{
  0%{transform: translate(0,0);}
  20%{transform: translate(-10px,0);}
  60%{transform: translate(10px,0);}
  100%{transform: translate(0,0);}
}